import React from 'react';

const Header = () => {

    const options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' };

    let date = new Date().toLocaleDateString(undefined, options).toUpperCase();

    return (
        <>
            <div className="col-12 col-md-6 col-xl-4 offset-md-3 offset-xl-4 header mt-3">
                <h1 className="text-center pb-3">TO DO F*CKING APP</h1>
            </div>

            <div className="col-12 text-center header">
                <p className="mt-4 mb-3 text-center date" style={{fontWeight: 400}}>{date}</p>
            </div>
        </>
    );
}

export default Header;