import React, { useState } from 'react';
import { useTransition, animated } from '@react-spring/web';

const Sidebar = ({ addTask }) => {

    const [visible, setVisible] = useState(false);

    const [task, setTask] = useState('');

    const transition = useTransition(visible, {
        from: { right: -320 },
        enter: { right: 0 },
        leave: { right: -320 },
        config: { duration: 500 }
    })

    return (
        <>
            {transition((style, item) => 
                item ? 
                <animated.div id="sidebar" style={style} className="container">
                    <div className="row">
                        <h1 className="col-12 text-center mt-5 sidebarTitle">ADD F*CKING TASK</h1>
                    </div>
                    <div className="row">
                        <textarea className="col-10 offset-1 mt-5 p-3" id="taskBox" autoFocus onChange={(e) => setTask(e.target.value)}></textarea>
                        <button type="button" className="btn btn-light col-10 offset-1 mt-5" onClick={() => {
                            
                            addTask(task);
                            
                            setVisible(!visible);
                            
                            setTask('');

                        }}>Add task</button>
                    </div>
                </animated.div> 
                : '')
            }

            {
                visible ? 
                <div className="addTaskBtn text-end">
                    <button type="button" className="btn btn-dark" onClick={() => setVisible(!visible)}>-</button>
                </div>
                :
                <div className="addTaskBtn text-end">
                    <button type="button" className="btn btn-dark" onClick={() => setVisible(!visible)}>+</button>
                </div>
            }
        </>
    );
}

export default Sidebar;