import React from 'react';

const Task = ({ tasks, deleteTask }) => {
    
    if (tasks.length === 0) {
        return (
            <p className="col-10 col-md-6 col-lg-4 offset-1 offset-md-3 offset-lg-4 text-center mt-5 py-3 zeroTasks">
                Click the "+" button to add some sh*t  <i className="bi bi-arrow-down-right"></i>
            </p>
        )
    }

    return (
        tasks.map((task, key) => {
            return (

                <div className="col-10 col-md-6 col-lg-4 offset-1 offset-md-3 offset-lg-4 mt-3 p-3 position-relative task" key={key}>
                    
                    <div className="row">

                        <p className="col-9 col-md-10 col-lg-10 text-nowrap pTask mb-0">{task}</p>

                        <button type="button" className="btn-close btn-close-white col-2 col-md-2 col-lg-1 offset-1 offset-lg-1" 
                        aria-label="Close" onClick={() => deleteTask(task)}></button>
                    
                    </div>

                </div>    
            )
        })
        
    );
}

export default Task;
