import React, { useState, useEffect } from 'react';
import Task from './Task';
import Sidebar from './Sidebar';

const TasksContainer = () => {

    const [tasks, setTasks] = useState([]);
    
    function addTask(input) {

        if (input === '') {
            return;
        }

        setTasks([...tasks, input]);
    }

    function deleteTask(task) {
        
        let taskPosition = tasks.indexOf(task);

        tasks.splice(taskPosition, 1);

        setTasks([...tasks]);
    }

    // Traigo del storage
    useEffect(() => {
            
        let tasksFromStorage = JSON.parse(localStorage.getItem('tasks'));

        if (tasksFromStorage) {
            setTasks(tasksFromStorage);
        }
        
    }, []);

    // Guardo en storage
    useEffect(() => {

        localStorage.setItem('tasks', JSON.stringify(tasks));

    }, [tasks])

    return (
        <div className="col-12 tasksContainer">
            <div className="row">
                <Task tasks={tasks} deleteTask={deleteTask} />
                <Sidebar addTask={addTask} />
            </div>
        </div>
    );
}

export default TasksContainer;